<template>
  <Transition name="fade-in">
    <div @click="toggleMenu()" v-show="isMenuOpen" id="sidebar-bg"></div>
  </Transition>

  <Transition name="fade-in-left">
    <div v-show="isMenuOpen" id="sidebar" :style="{backgroundImage: appMenuBg}">
      <AppMenu />
    </div>
  </Transition>

  <div id="main" :class="{noMarginLeft: !isMenuOpen}">
    <div id="navbar">
      <span @click="toggleMenu()" class="material-icons">menu</span>
      {{ $route.meta.title }}
    </div>

    <router-view/>
  </div>
</template>

<script>
import AppMenu from './components/AppMenu.vue'

export default {
  name: 'App',

  components: {
    AppMenu
  },

  created () {
    if (innerWidth > 768) {
      this.isMenuOpen = true
    }

    this.randomAppMenuBg()
  },

  data() {
    return {
      isMenuOpen: false,
      appMenuBg: 'none'
    }
  },

  watch: {
    $route: {
      immediate: true,

      // Change title when route changes
      handler(to) {
        document.title = to.meta.title || 'MTLKMS'
        
        if (innerWidth < 768) {
          this.isMenuOpen = false
        }
      }
    }
  },

  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen
    },

    randomAppMenuBg() {
      this.appMenuBg = `url(/img/app-menu-bg/app-menu-bg-${Math.floor(Math.random() * 15) + 1}.jpeg)`
    }
  }
}
</script>

